import React, { useState } from 'react';
import {
  Link
} from "react-router-dom";

export default function Navbar(props) {

  const [navbarActive, setnavbarActive ] = useState(false);

  const changeBackground = () => {
    if(window.scrollY >= 800){
      setnavbarActive(true)
    }else{
      setnavbarActive(false)
    }
  }
  window.addEventListener('scroll',changeBackground);

  return (
    <nav className={`py-2 navbar  navbar-expand-lg navbar-dark ${navbarActive? 'bg-primary fixed-top' : 'bg-transprent '}`}>
      <div className="container-fluid px-5">
        <a className="navbar-brand" href="#">
          <img className="img-fluid" src="img/logo2.png" alt="" style={{height:100}} />
        </a>


        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>


        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav me-auto ms-auto mb-2 mb-lg-0 ">
            <li className="nav-item">
              <a className="nav-link " href="#">
              <h2 className="nav-link active"  >Home</h2>
              </a>
            </li>
            <li className="nav-item">
            <a className="nav-link " href="#">
              <h2 className="nav-link "  >OpenSea</h2>
              </a>
            </li>
            <li className="nav-item">
            <a className="nav-link " href="#">
              <h2 className="nav-link "  >Discord</h2>
              </a>
            </li>

            <li className="nav-item">
            <a className="nav-link " href="#">
              <h2 className="nav-link ">Your Nft</h2>
              </a>
            </li>
            
          </ul>
        </div>

        <button className="btn btn-dark">Connect</button>
      </div>
    </nav>
  )
}
